<template>
  <div class="sys-header">
    <div class="sys-logo">
      <h1>重庆缺陷产品信息管理系统</h1>
      <h2>缺陷产品应用管理</h2>
    </div>
    <div class="sys-menu" id="sys-menu" v-loading="loading">
      <ul>
        <li v-for="m in menu" :key="m.id" v-curline="{curl: curLine}">
          <a href="javascript:void(0)" v-html="m.name" @click="toPage(m)"></a>
          <ul class="sys-menu-child" v-if="m.child.length > 0">
            <li v-for="n in m.child" :key="n.id" :class="{'active': n.name == nowMenu}">
              <a href="javascript:void(0)" v-html="n.name" @click="toPage(n, menu)"></a>
            </li>
          </ul>
        </li>
      </ul>
      <div class="curLine" :style="{ 'width': curLine.width + 'px' , 'left': curLine.left + 'px' }"></div>
    </div>
    <div class="sys-topbar">
      <ul>
        <li>
          <a href="javascript:void(0)"> <i class="icon-person"></i>{{user.name}}</a>
        </li>
        <li>
          <a href="javascript:void(0)" @click='goHome'> <i class="icon-home"></i>首页</a>
        </li>
        <li>
          <el-dropdown @command="openSystem">
            <span class="el-dropdown-link">
              <i class="icon-menu"></i>系统切换
            </span>
            <el-dropdown-menu slot="dropdown" id='select-menu'>
              <el-dropdown-item v-for="item in system" :key='item.id' :command='item'>{{item.name}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </li>
        <li>
          <a href="javascript:void(0)" @click="logout"> <i class="icon-logout"></i>退出</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import toPage from '@/mixins/toPage'
export default {
  data () {
    return {
      activeIndex: '1',
      activeIndex2: '1',
      loading: false,
      user: {},
      menu: [],
      system: [],
      curLine: {
        width: 0,
        left: 0,
        nowMenu: ''
      },
      nowMenu: ''
    }
  },
  mixins: [
    toPage
  ],
  created () {
    this.getUser()
    this.getMenu()
    this.getSys()
  },
  methods: {
    getUser () {
      let self = this
      this.$http.get({
        api: 'S_USERINFO',
        params: {},
        callback: (res) => {
          self.user = res
        }
      })
    },
    getMenu () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'S_MENU',
        params: {
          systemId: 3
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.menu = res
        }
      })
    },
    getSys () {
      let self = this
      this.$http.get({
        api: 'S_SYSINFO',
        params: {
          curSysId: 3
        },
        callback: (res) => {
          self.system = res
        }
      })
    },
    openSystem (command) {
      let link = command.url
      if (link) {
        let token = this.$store.getters.getToken
        window.location.href = link + '#/token/' + token
      } else {
        this.$bus.$emit('alert', {
          text: '链接出错！'
        })
      }
    },
    logout () {
      let self = this
      this.$http.get({
        api: 'S_LOGOUT',
        params: {},
        callback: (res) => {
          self.$store.commit('deleteToken')
          window.location.href = window._config.LOGIN_PAGE
        }
      })
    },
    goHome () {
      window.location.href = window._config.HOME_SYSTEM
    }
  },
  watch: {
    '$route' (val, oldVal) {
      if (!val.name) return
      this.curLine.nowMenu = val
    }
  }
}

</script>

<style scoped>
  .sys-header {
    z-index: 99;
  }

  .sys-topbar {
    border-left: 0px;
    background-color: initial;
  }

  .el-dropdown-link {
    cursor: pointer;
    color: #fff;
  }

</style>



