<template>
  <div class="sys-wraper">
    <!-- sys-header -->
    <top-menu />

    <!-- sys-path -->
    <div class="sys-path"> <i class="icon-home"></i> 互联网情报分析 &gt; {{ pageName }} </div>

    <!-- sys-main -->
    <router-view />
    <j-alert />
    <j-confirm />
  </div>
</template>

<script>
import TopMenu from '@/components/TopMenu'
import JAlert from '@/components/JAlert'
import JConfirm from '@/components/JConfirm'
export default {
  name: 'App',
  data () {
    return {
      defaultPage: false,
      pageName: '总览'
    }
  },
  components: {
    TopMenu,
    JAlert,
    JConfirm
  },
  created () {
    this.checkToken()
  },
  methods: {
    checkToken () {
      let token = this.$store.getters.getToken
      let newtoken = this.$route.params.token
      if (newtoken) {
        this.$store.commit('setToken', newtoken)
        this.$router.push('/index')
      }
      if (!token && !newtoken) {
        window.location.href = window._config.LOGIN_PAGE
      }
    }
  },
  watch: {
    '$route' (val, oldVal) {
      this.pageName = this.$route.meta
    }
  }
}

</script>

<style>
  .custom-el-message {
    top: 100px;
  }

  .highlight-keyword {
    color: red;
    font-weight: 800;
  }

  ::-webkit-scrollbar {
    width: 10px;
    height: 10px;
  }

</style>



