let lib = {
  isEmptyObject (o) {
    for (let i in o) {
      return false
    }
    return true
  },
  isEqual (obj, obj2) {
    let status = true
    if (this.isEmptyObject(obj)) {
      if (this.isEmptyObject(obj2)) {
        return true
      } else {
        return false
      }
    } else {
      if (this.isEmptyObject(obj2)) {
        return false
      }
    }
    for (let i in obj) {
      if (obj[i] !== obj2[i]) {
        status = false
        return
      }
    }
    return status
  },
  toThousands (num) {
    return (num || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
  },
  isInArray (arr, target) {
    let hash = false
    arr.map((o) => {
      if (o === target) hash = true
    })
    return hash
  },
  getUrlKey (name) {
    return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href))[1].replace(/\+/g, '%20')) || null
  },
  tip (x, y, name) {
    let el = document.querySelector('#tipDialog')
    if (!el) {
      let div = document.createElement('div')
      div.id = 'tipDialog'
      div.style = 'position:fixed;left:0;top:0;float:left;z-index:10;'
      div.innerHTML = `
        <div class="tiptext" id="tiptext" style="position:absolute;z-index:2;left:0;top:0;float:left;max-width:400px;height:auto;color:#fff;padding:5px; background:#000; opacity: 0.8; font-size:12px;border-radius:3px;word-break:keep-all;"></div>
      `
      document.body.append(div)
      el = document.querySelector('#tipDialog')
    }
    el.style.left = (x + 20) + 'px'
    el.style.top = y + 'px'
    el.querySelector('#tiptext').innerHTML = name
    if (window.setCapture) {
      window.setCapture()
      // 设置事件
      window.onmousemove = function (ev) {
        this.mouseMove(ev || event)
      }
    } else {
      window.addEventListener('mousemove', this.mouseMove, false)
    }
  },
  hideTip () {
    if (window.setCapture) {
      window.releaseCapture()
      // 设置事件
      window.onmousemove = null
    } else {
      window.removeEventListener('mousemove', this.mouseMove)
    }
    let el = document.querySelector('#tipDialog')
    if (el) el.parentNode.removeChild(el)
  },
  mouseMove (event) {
    let x = event.pageX
    let y = event.pageY
    // 拖拽元素随鼠标移动
    let el = document.querySelector('#tipDialog')
    el.style.left = (x + 20) + 'px'
    el.style.top = y - document.documentElement.scrollTop + 'px'
  },
  highLight (data, key) {
    if (key && data.length > 0) {
      data.map(o => {
        for (let i in o) {
          let replaceReg = new RegExp(key, 'g')
          let replaceString = '<span class="highlight-keyword">' + key + '</span>'
          if (o[i] && typeof o[i] === 'string') {
            o[i] = o[i].replace(replaceReg, replaceString)
          }
        }
      })
    }
    return data
  }
}

export default lib



